
import React from "react";
import Carousel from "react-multi-carousel";
import ReactStars from "react-rating-stars-component";
import "./style/review.css";
import "react-multi-carousel/lib/styles.css";
export default function Review({ reviews, type }) {
  let selectedReviews = reviews;
  if (type === 'favorite')
    selectedReviews = reviews.filter(r => r.favorite);

  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
      slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };
  return (
    <div className='text-center m-3  align-items-center' >
      <h4 className="text-danger text-center">Our Customer's Reviews</h4>

      <Carousel
        swipeable={true}
        draggable={true}
        showDots={false}
        responsive={responsive}
        ssr={true} // means to render carousel on server-side.
        infinite={true}
        // autoPlay={this.props.deviceType !== "mobile" ? true : false}
        // autoPlay={true}
        autoPlaySpeed={100}
        keyBoardControl={true}
        customTransition="all .5"
        transitionDuration={500}
        containerClass="carousel-container"
        removeArrowOnDeviceType={["tablet"]}
        // deviceType={this.props.deviceType}
        dotListClass="custom-dot-list-style"
        itemClass="carousel-item-padding-40-px"
      >
        {selectedReviews?.map(review => (
          <div>
            <div class="card card_back_col p-3 mx-2 Card_h_w">
              <div class="d-flex align-items-center mt-1 ">
                <ReactStars value={review?.rating} />
              </div>
              <div class="fw-bold r_title_look mb-2">
                {review?.reviewTitle}
              </div>
              <div class="fw-bold r_name_look mb-3  ">
                {review?.name}
              </div>
              <p class=" re_dis_look overflow-auto">
                {review?.feedback}
              </p>
            </div>
          </div>
        ))}
        {selectedReviews?.map(review => (
          <div>
            <div class="card card_back_col p-3 mx-2 Card_h_w">
              <div class="d-flex align-items-center mt-1">
                <ReactStars value={review?.rating} />
              </div>
              <div class="fw-bold r_title_look mb-2">
                {review?.reviewTitle}
              </div>
              <div class="fw-bold r_name_look mb-3  ">
                {review?.name}
              </div>
              <p class=" re_dis_look overflow-auto">
                {review?.feedback}
              </p>
            </div>
          </div>
        ))}
      </Carousel>
    </div>

  );
}
